import axios from 'axios';

const API_KEY = 'c01f14dcdb58e9cec669b1017a4d540c';
const BASE_URL = 'https://api.themoviedb.org/3';

export default class FilmsApiService {
  constructor() {
    this.searchQueryFilms = '';
    this.page = 1;
    this.pages = 0;
    this.totalItems = 0;
    this.with_genres = 0;
  }

  //      .
  async onFetchTopDayFilms() {
    const searchParams = new URLSearchParams({
      page: this.page,
    });

    const response = await axios.get(
      `${BASE_URL}/trending/movie/day?api_key=${API_KEY}&${searchParams}`,
    );
    const data = await response.data;
    this.totalPage = data.total_pages;
    this.totalItems = data.total_results;
    return data.results;
  }

  //      .
  async onFetchTopWeekFilms() {
    const searchParams = new URLSearchParams({
      page: this.page,
    });

    const response = await axios.get(
      `${BASE_URL}/trending/movie/week?api_key=${API_KEY}&${searchParams}`,
    );
    const data = await response.data;
    this.totalPage = data.total_pages;
    this.totalItems = data.total_results;
    return data.results;
  }

  //    /    (  ).
  async onFetchKeyWordFilms() {
    const searchParams = new URLSearchParams({
      query: this.searchQueryFilms,
      include_adult: false,
      page: this.page,
    });

    const response = await axios.get(
      `${BASE_URL}/search/movie?api_key=${API_KEY}&language=en-US&${searchParams}`,
    );
    const data = await response.data;
    this.totalPage = data.total_pages;
    this.totalItems = data.total_results;
    return data.results;
  }

  //    id  .
  async onFetchId() {
    const response = await axios.get(
      `${BASE_URL}/genre/movie/list?api_key=${API_KEY}&language=en-US`,
    );
    const data = await response.data;
    const genres = await data.genres;
    return genres;
  }

  //         (  :)).
  get query() {
    return this.searchQueryFilms;
  }

  //     /     .
  set query(newSearchQueryFilm) {
    this.searchQueryFilms = newSearchQueryFilm;
  }

  //         (    :)).
  get pageNumber() {
    return this.page;
  }

  //        (     :)).
  //     ,     1  4  10  ...  .
  set pageNumber(newPageNumber) {
    this.page = newPageNumber;
  }

  //      -   .
  get totalPage() {
    return this.pages;
  }

  //       pages    .
  set totalPage(newTotalPage) {
    this.pages = newTotalPage;
  }

  //     .
  incrementPageNumber() {
    this.page += 1;
  }

  //        (page=1).
  resetPageNumber() {
    this.page = 1;
  }

  resetTotalItems() {
    this.totalItems = 0;
  }

  //    ,       .
  emptySearchQueryFilm() {
    console.log('Field of search is empty, enter please keyword or words for begin search');
  }

  //    ,       .
  errorFilmSearch() {
    console.log('Search result not successful. Enter the correct movie name and try again');
  }

  //      
  async onfetchTrailers(id) {
    try {
      const url = `${BASE_URL}/movie/${id}/videos?api_key=${API_KEY}&language=en-US`;
      const response = await fetch(url);
      const data = response.json();
      return data;
    } catch (error) {
      console.error('Get state error: ', error.message);
    }
  }

  //    id  
  async onFetchId() {
    const response = await axios.get(
      `${BASE_URL}/genre/movie/list?api_key=${API_KEY}&language=en-US`,
    );
    const data = await response.data;
    const genres = await data.genres;
    return genres;
  }

  //    /    (  ).
  async onFetchGenresFilms() {
    const searchParams = new URLSearchParams({
      with_genres: this.with_genres,
      include_adult: false,
      page: this.page,
    });

    const response = await axios.get(
      `${BASE_URL}/discover/movie?api_key=${API_KEY}&language=en-US&${searchParams}`,
    );

    const data = await response.data;
    this.totalPage = data.total_pages;
    this.totalItems = data.total_results;
    return data.results;
  }

  //       .
  get genre() {
    return this.with_genres;
  }

  //       .
  set genre(genreId) {
    this.with_genres = genreId;
  }
}
